import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import PubSub, { topics } from '../../lib/pubsub';

import ImageSlider from '../imageSlider';
import WorkoutComments from '../workout/comments';
import WorkoutAddComment from '../workout/commentAdd';

import PublicTeam from '../team/public';

import TeamConnectBoxes from './connect-boxes';
import TeamConnectConfirm from './connect-confirm';

import Ad from '../ad/';

import mu from '../../lib/musher-util';

function displayDistance(distanceKm) {
	if (distanceKm < 5) {
		return util.format(distanceKm, 2);
	}
	if (distanceKm < 10) {
		return util.format(distanceKm, 1);
	}
	return util.format(distanceKm, 1);
}

const initialState = {
	showAddComment: {},
    viewImageIdx: {},
    likeWorkout: {},
    idxSuggestion: 0,
    idxAd: 4,
};

@observer
class TeamPublicFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.inputRef = {};
        this.subscriptions();
    }

    subscriptions = () => {
        // PubSub.subscribe(topics.APP_RESUME, async () => {
        //     await this.loadAll();
        // });
        // PubSub.subscribe(topics.REFRESH_PAGE, async () => {
        //     await this.loadAll();
        // });
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

	likeWorkout = async (e) => {
        const workoutId = parseInt(e.target.closest('.workoutImage').dataset.id, 10);
		const { workoutStore } = this.props.stores;

        const { likeWorkout } = this.state;
        likeWorkout[workoutId] = true;
        this.setState({ likeWorkout });
        setTimeout(() => {
            const { likeWorkout } = this.state;
            delete likeWorkout[workoutId];
            this.setState({ likeWorkout });
        }, 1200);

		const response = await workoutStore.likeWorkout({ id: workoutId });
        if (response && response.status === 200) {
            this.addLikeToFeed(workoutId, response.data);
		}
	}

    addCommentToFeed = ({ type, workout, response, commentId, replyId }) => {
        // console.log('addCommentToFeed', { workout, response, commentId });
        const { teamStore } = this.props.stores;
        switch (type) {
            case 'likeComment':
                teamStore.addLikeToComment({ id: workout.id, data: response.data, commentId });
                break;
            case 'likeReply':
                teamStore.addLikeToCommentReply({ id: workout.id, data: response.data, commentId, replyId });
                break;
        
            default:
                if (commentId) {
                    teamStore.addCommentToWorkoutComment({ id: workout.id, data: response.data, commentId });
                } else {
                    teamStore.addCommentToWorkout({ id: workout.id, data: response.data });
                }
                break;
        }
    }

    addLikeToFeed = (id, data) => {
        const { teamStore } = this.props.stores;
        teamStore.addLikeToWorkout({ id, data });
    }

	toggleAddComment = (e) => {
		const workoutId = e.target.closest('button').dataset.id;
		const { showAddComment } = this.state;
        if (showAddComment[workoutId]) {
            delete showAddComment[workoutId];
        } else {
            showAddComment[workoutId] = true;
        }
		this.setState({
			showAddComment,
		}, () => {
			if (this.inputRef[workoutId]) {
				this.inputRef[workoutId].focus();
			}
		});
	}

    viewImage = (e) => {
		const { id, idx } = e.target.closest('img').dataset;
        const { viewImageIdx } = this.state;
        viewImageIdx[id] = idx;
        this.setState({ viewImageIdx });
    }

    async loadTeamsPublicFeed(props = this.props) {
		const { teamStore } = props.stores;
		await teamStore.loadPublicFeed();
	}

    async loadAll() {
		await this.loadTeamsPublicFeed();
    }

    shareLink = (e) => {
        const { url, title, text } = e.target.closest('button').dataset;
        const { appState } = this.props.stores;
        appState.shareUrl({ url, title, text });
    }

	componentDidMount() {
        this.loadAll();
	}

    render() {
        const { showAddComment, showDate, viewImageIdx = {}, likeWorkout, idxSuggestion, idxAd } = this.state;
        const { teamStore, userStore, appState } = this.props.stores;
        const { user, isAdmin } = userStore;
        const { language = 'no' } = user;
        const { publicTeamsFeed } = teamStore;
        const { darkmode, hasShareApi, path } = appState;
        let hasFollowSugguestions = false;
        let hasAd = false;
        return (
            <div class='w-100'>
                <TeamConnectConfirm stores={this.props.stores} />

                {publicTeamsFeed && publicTeamsFeed.map((feed, idx) => {
                    const team = teamStore.findPublicTeam(feed.team) || {};
                    let image;
                    if (team.image && team.image.s3SmallLink) {
                        image = team.image;
                    } else if (team.images && team.images[0] && team.images[0].s3SmallLink) {
                        image = team.images[0];
                    }
                    const { likes, comments } = feed;
                    const hasLikedWorkout = feed.likes && feed.likes.findIndex(e => e.user === user.id) > -1;
                    const totalFollowers = team && team.followers ? team.followers.length : 0;

                    const isYourTeam = team.members && team.members.indexOf(user.id) > -1;
                    const isFollowing = team && team.followers && team.followers.indexOf(user.id) > -1;
                    const hasRequestedFollow = team.followRequests && team.followRequests.findIndex(e => e.id === user.id) > -1;
                    const images = [];
                    if (feed.mapImage) {
                        images.push({
                            ...feed.mapImage,
                            type: 'map',
                        });
                    }
                    if (feed.images) {
                        const imgs = toJS(feed.images);
                        images.push(...imgs);
                    }
                    const imageIdx = viewImageIdx[feed.id] ? viewImageIdx[feed.id] : 0;
                    const currentImage = images && images[imageIdx] ? images[imageIdx] : null;
                    // if (feed.gpxInfo && feed.gpxInfo.svgMap) {
                    //     images.push({
                    //         svg: feed.gpxInfo.svgMap,
                    //         stroke: '#0275d8',
                    //     });
                    // }
                    // if (feed.gpxInfo && feed.gpxInfo.svgElevation) {
                    //     images.push({
                    //         svg: feed.gpxInfo.svgElevation,
                    //         stroke: '#5cb85c',
                    //     });
                    // }

                    const { currentLocationGeocode = {}, yrWeather = [] } = feed;

                    if (!team || !team.name) {
                        return '';
                    }

                    if (idx === idxSuggestion) {
                        hasFollowSugguestions = true;
                    }
                    if (idx === idxAd) {
                        hasAd = true;
                    }

                    return (<>

                        {idx === idxSuggestion && <>
                            <TeamConnectBoxes stores={this.props.stores} />
                        </>}
                        {idx === idxAd && <>
                            <Ad stores={this.props.stores} path={path} />
                        </>}

                        <div class='row pb-4 bg-light'>
                            <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative rounded rounded-lg py-3 bg-${darkmode ? 'black' : 'white'} shadow-sm`}>

                                <div class='d-flex'>
                                    <div class='text-center'>
                                        <div
                                            class={`text-center border rounded-circle imageRounded imageRoundedSmall`}
                                            style={image ? `background-image: url("${image.s3SmallLink}"); background-size: cover;` : ''}>
                                            {!image && <i class='fas fa-users text-muted mt-2' style='font-size: 20px;' />}
                                        </div>
                                    </div>
                                    <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                                        <h5 class='mb-1' style='line-height: 1.0em;'>
                                            {team.public ? <>
                                                <a href={`/teams/public/${team.uuidv4}`}>{team.name}</a>
                                            </> : <>
                                                {team.name}
                                            </>}
                                        </h5>
                                        <div>
                                            {feed.date && <small class='text-muted' onClick={this.toggleDate}>
                                                {showDate ? util.formatDate(feed.date, { locale: 'no-NB', hour12: false, hour: '2-digit', minute: '2-digit' }) : util.formatDistance(feed.date, new Date(), { locale: 'no-NB' })}
                                            </small>}
                                            {feed.public === 1 ? <>
                                                <small class='ml-2 text-muted'>
                                                    <i class='fas fa-globe-europe' />
                                                </small>
                                            </> : <>
                                                <small class='ml-2 text-muted'>
                                                    <i class='fas fa-users' /> ({util.format(totalFollowers)})
                                                </small>
                                            </>}
                                            <small class='text-muted'>
                                                {feed.equipment > 0 && <><i class='fas fa-tools ml-2' /> {userStore.findEquipment(feed.equipment) || feed.equipment}</>}
                                                {/* {feed.distanceKm > 0 && <><i class='fas fa-road ml-2' /> {displayDistance(feed.distanceKm)}km</>}
                                                {feed.elevation > 0 && <><i class='fas fa-mountain ml-2' /> {util.format(feed.elevation, 0)}m</>}
                                                {feed.speedAvg > 0 && <><i class='fas fa-tachometer-alt ml-2' /> {util.format(feed.speedAvg, 1)}km/t</>} */}
                                                {feed.dogs > 0 && <><i class='fas fa-dog ml-2' /> {feed.dogs.length}</>}
                                                {feed.track > 0 && <><i class={`fas fa-map-marked text-${feed.track > 0 ? 'success' : 'danger'} ml-2`} /></>}
                                                {/* {feed.dogsLeft > 0 && <><i class={`fas fa-crosshairs text-${feed.dogsLeft && feed.dogsRight ? 'success' : 'danger'} ml-2`} /></>} */}
                                                {feed.sledWeight > 0 && <>
                                                    <i class='fas fa-sleigh ml-2' /> {util.format(feed.sledWeight, 0)} kg
                                                </>}
                                                {feed.temperature !== 0 && <>
                                                    <i class='fas fa-temperature-low ml-2' /> {feed.temperature}°C
                                                </>}
                                                {/* {feed.kcalBurnedTotal && <><i class='fas fa-balance-scale' /> {util.format(feed.kcalBurnedTotal)} kcal</>}
                                                {feed.kcalMeatGramsTotal && <><i class='fas fa-drumstick-bite ml-2' /> {util.format(feed.kcalMeatGramsTotal / 1000, 1)} kg</>}
                                                {feed.kcalSalmonGramsTotal && <><i class='fas fa-fish ml-2' /> {util.format(feed.kcalSalmonGramsTotal / 1000, 1)} kg</>}
                                                {feed.kcalFatGramsTotal && <><i class='fas fa-piggy-bank ml-2' /> {util.format(feed.kcalFatGramsTotal / 1000, 1)} kg</>} */}
                                            </small>
                                            {isAdmin && currentLocationGeocode && currentLocationGeocode.formatted_address && <>
                                                <small class='ml-2 text-muted'>
                                                    <i class='fas fa-map-marker-alt' /> {currentLocationGeocode.formatted_address}
                                                </small>
                                            </>}
                                        </div>
                                    </div>
                                </div>

                                <div class='row mt-2'>
                                    <div class='clearfix col-12 font-weight-bold' style='font-size: 1.2em;'>
                                        <Markdown markdown={mu.replaceImages(feed.name, feed.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                    </div>
                                    <div class='clearfix col-12'>
                                        <Markdown markdown={mu.replaceImages(feed.comment, feed.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                    </div>
                                </div>

                                <div class='row mt-1'>
                                    {feed.distanceKm > 0 && <div class='col-3'>
                                        <div class='text-overflow'>
                                            <small><i class='fas fa-road' /> <Text id='workout.calendar.distance'>Distanse</Text></small>
                                        </div>
                                        <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
                                            {displayDistance(feed.distanceKm)}km
                                        </div>
                                    </div>}
                                    {feed.elevation > 0 && <div class='col-3'>
                                        <div class='text-overflow'>
                                            <small><i class='fas fa-mountain' /> <Text id='workout.calendar.ascend'>Høydemeter</Text></small>
                                        </div>
                                        <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
                                            {util.format(feed.elevation, 0)}m
                                        </div>
                                    </div>}
                                    {feed.duration > 0 && <div class='col-3'>
                                        <div class='text-overflow'>
                                            <small><i class='fas fa-clock' /> <Text id='workout.calendar.duration'>Varighet</Text></small>
                                        </div>
                                        <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
                                            {util.secToHms(feed.duration, true)}
                                        </div>
                                    </div>}
                                    {feed.speedAvg > 0 && <div class='col-3'>
                                        <div class='text-overflow'>
                                            <small><i class='fas fa-tachometer-alt' /> <Text id='workout.calendar.avg-speed'>Snittfart</Text></small>
                                        </div>
                                        <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
                                            {util.format(feed.speedAvg, 1)}km/t
                                        </div>
                                    </div>}
                                </div>

                                {/* <div class='row'>
                                    <div class='row mb-3'>
                                        {feed.gpxInfo && feed.gpxInfo.svgMap && <>
                                            <div class='col-6 mt-auto text-center overflow-hidden'>
                                                <svg viewBox='0 0 300 300' style='width: 150px; height: 150px;'>
                                                    <path d={`${feed.gpxInfo.svgMap}`} style='fill: none; stroke: #0275d8; stroke-width: 5; opacity: 0.5;' />
                                                </svg>
                                                <small class='text-muted'>Track</small>
                                            </div>
                                        </>}
                                        {feed.gpxInfo && feed.gpxInfo.svgElevation && <>
                                            <div class='col-6 mt-auto text-center overflow-hidden'>
                                                <svg viewBox='0 0 300 200' style='width: 150px; height: 100px;'>
                                                    <path d={`${feed.gpxInfo.svgElevation}`} style='fill: none; stroke: #5cb85c; stroke-width: 5; opacity: 0.5;' />
                                                </svg>
                                                <small class='text-muted'>Elevation</small>
                                            </div>
                                        </>}
                                    </div>
                                </div> */}

                                <div class='container-fluid px-0 mt-1'>
                                    {currentImage && <div class='clearfix row position-relative'>
                                        <div
                                            class={`workoutImage col-12 px-0 overflow-hidden`}
                                            style={`max-height: 50vh;`}
                                            onDblclick={this.likeWorkout}
                                            data-id={feed.id}
                                        >
                                            <img loading='lazy' class={`img-fluid`} style='min-width: 100px;' src={currentImage.s3XLargeLink} />
                                        </div>
                                        {/* {isCordova && <span class='position-absolute' style='top: 5px; right: 10px;'>
                                            <a href={`fb://facewebmodal/f?href=${currentImage.s3XLargeLink}`} target='_blank' rel='noreferrer' class='text-white'><i class='fab fa-facebook-square' /></a>
                                        </span>} */}
                                        {likeWorkout[feed.id] && <span class='likeHeart position-absolute text-white display-1' style='top: calc(50% - 50px); left: calc(50% - 50px); width: 100px; height: 100px;'>
                                            <i class='fas fa-heart likeStory' />
                                        </span>}
                                    </div>}
                                    {images && images.length > 1 && <div class='clearfix d-flex flex-wrap row'>
                                        {images.map((img, idx) => {
                                            // if (img.svg) {
                                            //     return (<>
                                            //         <div class={`col-4 mt-auto`}>
                                            //             <svg viewBox='0 0 300 300' style='width: 150px; height: 150px;'>
                                            //                 <path d={`${img.svg}`} style={`fill: none; stroke: ${img.stroke || '#0275d8'}; stroke-width: 5; opacity: 0.5;`} />
                                            //             </svg>
                                            //         </div>
                                            //     </>);
                                            // }
                                            return (<>
                                                <div
                                                    class={`col px-0 overflow-hidden`}
                                                    style={`max-height: 100px;`}                                                    
                                                >
                                                    <img loading='lazy' class={`img-fluid border`} style='min-width: 100px;' src={img.s3SmallLink} onClick={this.viewImage} data-id={feed.id} data-idx={idx} />
                                                </div>
                                            </>);
                                        })}
                                    </div>}
                                </div>

                                <div class='mt-2'>
                                    {likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                        <i class='fas fa-thumbs-up text-primary' /> {likes.length}
                                    </small>}
                                    {comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                                        <i class='fas fa-comment text-primary' /> {comments.length}
                                    </small>}
                                </div>

                                {!isYourTeam && !isFollowing && !hasRequestedFollow && <div class='clearfix mb-2'>
                                    <PublicTeam stores={this.props.stores} team={team} buttonOnly={true} />
                                </div>}

                                <div>
                                    <div class='row'>
                                        <div class='col text-center'>
                                            <button type='button' class={`workoutImage btn btn-sm btn-outline-${hasLikedWorkout ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedWorkout ? 'text-primary' : ''} text-overflow`}
                                                data-id={feed.id}
                                                onClick={this.likeWorkout}
                                                // disabled={hasLikedWorkout}
                                            >
                                                <i class={`${hasLikedWorkout ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> <Text id='comments.like'>Lik</Text>
                                            </button>
                                        </div>
                                        <div class='col text-center'>
                                            <button type='button' class='btn btn-sm btn-outline-secondary btn-lg btn-block text-overflow'
                                                data-id={feed.id}
                                                onClick={this.toggleAddComment}
                                            >
                                                <i class='fas fa-comment' /> <Text id='comments.comment-add'>Kommentar</Text>
                                            </button>
                                        </div>
                                        {hasShareApi ? <>
                                            <div class='col text-center'>
                                                <button class='btn btn-sm btn-outline-secondary btn-lg btn-block text-overflow'
                                                    onClick={this.shareLink}
                                                    data-url={`https://themusher.app/share/workout/${feed.uuidv4}`}
                                                    data-title={`title: ${feed.title}`}
                                                >
                                                    <i class='fas fa-share' /> <Text id='story.share'>Del</Text>
                                                </button>
                                            </div>
                                        </> : <>
                                            <div class='col text-center'>
                                                <a class='btn btn-sm btn-outline-secondary btn-lg btn-block text-overflow'
                                                    href={`https://themusher.app/share/workout/${feed.uuidv4}`}
                                                    target='_blank'
                                                    rel='noreferrer'
                                                >
                                                    <i class='fas fa-share' /> <Text id='story.share'>Del</Text>
                                                </a>
                                            </div>
                                        </>}
                                    </div>
                                </div>

                                <div class='mt-2'>
                                    {showAddComment[feed.id] && <WorkoutAddComment
                                        stores={this.props.stores}
                                        workout={feed}
                                        inputRef={c => this.inputRef[feed.id] = c}
                                        inputRefObject={this.inputRef}
                                        col='12'
                                        offset='0'
                                        showAvatar={true}
                                        extraClass='px-0'
                                        callback={this.addCommentToFeed}
                                    />}
                                    <WorkoutComments
                                        stores={this.props.stores}
                                        workout={feed}
                                        col='12'
                                        offset='0'
                                        compact={false}
                                        callback={this.addCommentToFeed}
                                    />
                                </div>
                            </div>

                        </div>
                    </>);
                })}
                {!hasFollowSugguestions && <>
                    <TeamConnectBoxes stores={this.props.stores} />
                </>}
                {!hasAd && <>
                    <Ad stores={this.props.stores} path={path} />
                </>}
            </div>
        );
    }
}

export default TeamPublicFeed;
